import { request, requestDownload } from 'utils';

const prefix = '/api/finance';

export const getAdmissionReceipts = async ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  studentCode,
  semesterId,
  yearEduId,
  paymentTime,
  fromDate,
  toDate,
  receiptConfigId,
  paymentTypeId,
  fOId,
  casherId,
  invoiceNo,
  isCancelled,
  isPayment,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = false,
} = {}) => {
  const result = await request({
    prefix,
    url: '/Receipt/SearchReceipt',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      studentCode,
      semesterId,
      yearEduId,
      paymentTime,
      fromDate,
      toDate,
      receiptConfigId,
      paymentTypeId,
      fOId,
      casherId,
      invoiceNo,
      isCancelled,
      isPayment,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });
  if (result.data && result.data.items) {
    result.data.items.forEach(item => {
      item.key = `${item.yearEduId}_${item.trainTypeId}_${item.studyFieldId}_${item.receivableId}`;
    });
  }
  return result;
};

export const cancelReceipt = async receiptId => {
  await request({
    prefix,
    url: `/Receipt/${receiptId}/Cancel`,
    method: 'PUT',
    params: {},
    data: {},
  });
};

export const exportExcel = async ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  studentCode,
  semesterId,
  yearEduId,
  paymentTime,
  fromDate,
  toDate,
  receiptConfigId,
  paymentTypeId,
  fOId,
  casherId,
  invoiceNo,
  isCancelled,
  isPayment,
}) =>
  requestDownload({
    defaultFileName: 'Liệt kê biên lai.xlsx',
    prefix,
    url: '/Receipt​/ExportReceipt',
    method: 'POST',
    data: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      studentCode,
      semesterId,
      yearEduId,
      paymentTime,
      fromDate,
      toDate,
      receiptConfigId,
      paymentTypeId,
      fOId,
      casherId,
      invoiceNo,
      isCancelled,
      isPayment,
    },
  });



